import React from "react"
import { Link } from "gatsby"
import Layout from "../components/layout"
import SEO from "../components/seo"
import "../styles/styles.css"
import ScrollToTop from "react-scroll-to-top"
import loadable from "@loadable/component"

const LA8Component = loadable(() => import("../components/la8Component"))

const LanguageArts8 = ({ data }) => {

  return (

    <Layout>
      <SEO
        title="8th Grade Language Arts"
        description="Daily assignments for 8th grade language arts"
        image="/logo.png"
        pathname="/languagearts8"
      />

      <ScrollToTop smooth color="#78CCC7"/>

      <section className="block-circles">
        <div className="circle one"
             style={{ backgroundColor: '#83cae6', transform: "translate3d(0px, 0px, 0px) scale(0.9921, 1)", opacity: "0.9881" }}/>
        <div className="circle two" style={{ backgroundColor: '#83cae6', transform: "translate(0px, 0px)", opacity: 1 }}/>
        <div className="hero-wrapper pbn hero">
          <h1 className="hero intro in-left">Language Arts</h1>
          <div className="hero info-block info-block-line in-left" style={{ marginBottom: "4em" }}>
            <Link to="/eighth" className='gradeLink'><h2 className="hero in-left">8th Grade</h2></Link>
          </div>
        </div>
      </section>
      
       <LA8Component/>

    </Layout>
  )
}
export default LanguageArts8